.content {
grid-area: c;
background-color: #7496d4;
}

.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}
.dialogs_users {
  padding: 10px;
}
.dialogs_users .active {
  color: #a3c702;
  text-shadow: 1px 1px 3px #a3c702;
}

.messages {
  padding: 10px;
}

.user {
  font-size: 20px;
  font-weight: bold;
}
